@charset "UTF-8";
body, ul, li, a, h1, h2, h3, h4 {
  font-family: "ClashGrotesk-Variable";
}

p {
  color: #010101;
}

.slider-wrap, header {
  @apply pl-[60px]
  pr-[60px];
}

.block-features {
  @apply pt-[150px]
    pl-[60px]
    pr-[60px];
}
.block-features .wp-block-columns {
  @apply mb-0;
}

@media only screen and (max-width: 991px) {
  .slider-wrap, header {
    @apply pl-[20px]
    pr-[20px];
  }
  .block-features {
    @apply pt-[75px]
    pl-[20px]
    pr-[20px];
  }
  .block-features .wp-block-columns {
    @apply mb-0;
  }
}
.wp-block-post-template {
  display: grid;
  list-style: none;
  margin-bottom: 0;
  margin-top: 0;
  max-width: 100%;
  padding: 0;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
}
.wp-block-post-template li {
  margin-bottom: 30px;
  background-color: #ffffff;
  border-radius: 5px;
}
.wp-block-post-template li h2 {
  font-weight: 500;
  font-size: 24px;
  text-transform: capitalize;
}
.wp-block-post-template li .wp-block-column:nth-child(2) {
  padding-top: 20px;
  padding-bottom: 20px;
}

