  src: url("../scss/fonts/ClashGrotesk-Bold.woff2") format("woff2"), url("../scss/fonts/ClashGrotesk-Bold.woff") format("woff"), url("../scss/fonts/ClashGrotesk-Bold.ttf") format("truetype");
