@font-face {
  font-family: "ClashGrotesk-Variable";
  src: url("../scss/fonts/ClashGrotesk-Variable.woff2") format("woff2"), url("../scss/fonts/ClashGrotesk-Variable.woff") format("woff"), url("../scss/fonts/ClashGrotesk-Variable.ttf") format("truetype");
  font-weight: 200 700;
  font-display: swap;
