}
.block-header {
  @apply flex
  justify-between
  items-center
  pt-[30px]
  pb-0;
}
.block-header .menu-links {
  @apply ml-auto;
}
.block-header .menu-links ul {
  @apply flex
      items-center
      justify-end;
}
.block-header .menu-links ul li {
  @apply mr-[30px]
        text-center;
}
.block-header .menu-links ul li a {
  @apply text-[16px];
}
.block-header .menu-links ul li:hover {
  @apply font-medium
          text-[#7b69e9];
}
.block-header .menu-links ul li:hover::after {
  content: "";
  @apply block
          w-full
          h-[10px]
          bg-no-repeat
          bg-contain;
  background-image: url("../assets/images/underline.png");
}
.block-header .desk-cta {
  @apply px-[30px]
    py-[15px]
    border-2
    border-[#F4AB1E]
    rounded-[10px]
    transition-all
    duration-300;
}
.block-header .desk-cta:hover {
  @apply bg-[#7b69e9]
      text-white
      border-[#7b69e9];
}
.block-header .hamburger-cta {
  @apply hidden;
}
.block-header .close-menu-cta {
  @apply hidden;
}

.mobile-menu-items {
  @apply hidden;
}

@media (max-width: 991px) {
  header {
    @apply fixed
    bg-white
    z-[999]
    w-full;
  }
  .block-header {
    @apply items-center;
  }
  .block-header .menu-links,
  .block-header .desk-cta {
    @apply hidden;
  }
  .block-header .hamburger-cta {
    @apply flex
      flex-col
      cursor-pointer;
  }
  .block-header .hamburger-cta span {
    @apply block
        w-[30px]
        h-[5px]
        mb-[6px]
        bg-[#7b69e9];
  }
  .block-header .close-menu-cta {
    @apply rotate-45
      cursor-pointer;
  }
  .block-header .close-menu-cta span {
    @apply text-[60px]
        font-medium
        text-[#7b69e9];
  }
  .mobile-menu-items {
    @apply flex
    flex-col
    ml-0
    mr-0
    mt-[10px];
  }
  .mobile-menu-items .mobile-links {
    @apply pt-[30px]
      pl-[30px]
      flex-col
      bg-[#7b69e9]
      hidden;
    height: calc(100% - 180px);
  }
  .mobile-menu-items .mobile-links li {
    @apply mb-[30px];
  }
  .mobile-menu-items .mobile-links li a {
    @apply text-white;
  }
  .mobile-menu-items .mob-cta {
    @apply flex
      justify-center
      items-center
      w-full
      h-[60px]
      text-white
      bg-[#F4AB1E]
      cursor-pointer
      transition-all
      duration-300;
  }
  .mobile-menu-items .mob-cta a {
    @apply font-semibold
        text-[22px];
  }
  .mobile-menu-items .mob-cta:hover {
    @apply bg-[#7b69e9];
  }
}
.block-section-title {
  @apply flex
  justify-center
  flex-col
  items-center
  mb-[60px];
}
.block-section-title h2 {
  @apply relative
    text-[clamp(32px,5vw,60px)]
    m-0
    text-black
    leading-snug
    font-semibold
    text-center;
  max-width: 60%;
}
.block-section-title p {
  @apply m-0
    mb-[20px]
    leading-[35px]
    text-2xl
    font-normal
    text-center;
}

.block-text-and-image {
  @apply bg-no-repeat
  bg-right
  bg-contain
  flex
  flex-col
  md:flex-row
  items-stretch
  w-full
  max-w-full
  pt-[100px]
  pb-[100px]
  mx-auto
  mt-[60px]
  mb-0;
}
.block-text-and-image .component-left {
  @apply flex
    flex-col
    justify-center
    basis-[60%]
    text-black
    p-[20px];
}
.block-text-and-image .component-left .pre-title {
  @apply uppercase
      font-semibold
      mb-[20px]
      font-bold
      text-gray-800;
}
.block-text-and-image .component-left .h1-first, .block-text-and-image .component-left .h1-second {
  @apply relative
      text-[clamp(32px,5vw,60px)]
      m-0
      text-black
      leading-snug
      font-semibold;
}
.block-text-and-image .component-left .h1-second::after {
  content: "";
  @apply absolute
        right-[20%]
        bottom-[0px]
        w-[70%]
        h-[15px]
        bg-no-repeat
        bg-contain;
  background-image: url("../assets/images/underline.png");
}
.block-text-and-image .component-left .text-content p {
  @apply m-0
        mb-[20px]
        leading-[35px]
        font-normal
        max-w-[80%];
}
.block-text-and-image .component-left .text-content ul {
  @apply mt-[20px]
        mb-0;
}
.block-text-and-image .component-left .text-content ul li {
  @apply text-[24px]
          mt-[15px]
          mb-[30px]
          list-none
          leading-[15px]
          relative
          pl-[40px];
}
.block-text-and-image .component-left .text-content ul li::before {
  content: "";
  @apply absolute
            left-0
            top-[-5px]
            w-[25px]
            h-[25px]
            bg-no-repeat
            bg-contain;
  background-image: url("../assets/images/checkmark.png");
}
.block-text-and-image .component-left .form {
  @apply rounded-2xl
      p-[20px];
  border: 1px solid #EFEEE9;
}
.block-text-and-image .component-left .form input {
  @apply w-[70%]
        text-[18px]
        rounded-xl
        p-[20px]
        bg-[#F7F6F3]
        mr-2;
}
.block-text-and-image .component-left .form input::placeholder {
  color: #010101;
}
.block-text-and-image .component-left .form button {
  width: calc(30% - 20px);
  @apply text-[16px]
        tracking-[1px]
        text-white
        font-semibold
        rounded-xl
        bg-[#7B69E9]
        p-[20px];
}
.block-text-and-image .component-left .disclaimer {
  @apply mt-6
      text-2xl;
}
.block-text-and-image .component-left .button-set {
  @apply ml-0
      mt-[40px];
}
.block-text-and-image .component-left .button-set .btn-info {
  @apply ml-0;
}
.block-text-and-image .component-right {
  @apply basis-[40%]
    aspect-[1/1]
    bg-no-repeat
    bg-[90%];
}
@media (max-width: 768px) {
  .block-text-and-image .component-right {
    @apply aspect-auto
      h-[300px];
  }
}

@media only screen and (max-width: 991px) {
  .block-text-and-image {
    padding-bottom: 0;
    padding-top: 200px;
    margin-top: 180px;
    background-position: top;
  }
  .block-text-and-image .component-left {
    padding-top: 0;
    padding-bottom: 0;
    background: rgba(255, 255, 255, 0.2);
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
    border: 1px solid rgba(255, 255, 255, 0.3);
  }
  .block-text-and-image .form {
    flex-flow: column;
    display: flex;
    width: 100%;
  }
  .block-text-and-image .form input, .block-text-and-image .form button {
    width: 100% !important;
    margin-bottom: 20px;
  }
}
.block-card-vertical {
  height: calc(100% - 2rem);
  @apply flex-col
  bg-white
  text-center
  rounded-[20px]
  shadow-[1px_2px_22px_-15px_rgba(66,68,90,1)];
}
.block-card-vertical .card-image {
  height: 70%;
  @apply bg-cover
    bg-no-repeat
    w-full;
}
.block-card-vertical .block-card-vertical-data {
  height: 30%;
  @apply p-[20px]
    text-left
    relative;
}
.block-card-vertical .block-card-vertical-data h3 {
  @apply text-[24px]
      font-medium
      m-[5px_0];
}
.block-card-vertical .block-card-vertical-data p {
  @apply text-[16px]
      text-[#010101];
}
.block-card-vertical .block-card-vertical-data::after {
  content: "";
  @apply block
      w-[10px]
      h-[10px]
      mt-[10px]
      absolute
      right-[20px]
      top-[30px];
  background: url("./images/plusIcon.png") no-repeat center/cover;
}
.block-card-vertical .block-card-vertical-data .author {
  display: grid;
  gap: 10px;
  margin-top: 10px;
  grid-template-columns: 40px 1fr;
  position: absolute;
  top: -120px;
  background: #FFFFFF;
  padding: 10px;
  border-radius: 50px;
  padding-right: 20px;
}
.block-card-vertical .block-card-vertical-data .author h5 {
  color: #010101;
  font-weight: 500;
}

@media only screen and (max-width: 991px) {
  .block-card-vertical {
    height: 420px;
  }
  .block-card-vertical .block-card-vertical-data {
    height: 120px;
  }
}
.block-card-horizontal {
  @apply flex
  bg-white
  text-center
  rounded-[20px]
  mb-8
  shadow-[1px_2px_22px_-15px_rgba(66,68,90,1)];
}
.block-card-horizontal .card-image {
  -webkit-border-top-left-radius: 20px;
  -webkit-border-bottom-left-radius: 20px;
  -moz-border-radius-topleft: 20px;
  -moz-border-radius-bottomleft: 20px;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  @apply bg-cover
    bg-no-repeat
    w-full
    basis-[40%]
    h-auto;
}
.block-card-horizontal .block-card-horizontal-data {
  @apply p-[20px]
    text-left
    basis-[60%]
    content-center
    relative;
}
.block-card-horizontal .block-card-horizontal-data h3 {
  @apply w-[70%]
      text-[24px]
      font-medium
      m-[5px_0];
}
.block-card-horizontal .block-card-horizontal-data p {
  @apply text-[16px]
      text-[#010101];
}
.block-card-horizontal .block-card-horizontal-data::after {
  content: "";
  @apply block
      w-[10px]
      h-[10px]
      mt-[10px]
      absolute
      right-[20px]
      top-[30px];
  background: url("./images/plusIcon.png") no-repeat center/cover;
}
.block-card-horizontal .block-card-horizontal-data .author {
  display: grid;
  gap: 10px;
  margin-top: 10px;
  grid-template-columns: 40px 1fr;
}
.block-card-horizontal .block-card-horizontal-data .author h5 {
  color: #010101;
  font-weight: 500;
}

